import React, { Component } from 'react';


class SidebarPost extends Component {
  render() {
    return (
        <div className="sidebar sidebar__post">
          <span className="sidebar__title">   </span>
          <div className="sidebar__item">
            <span className="sidebar__post-title">  - </span>
            <span className="sidebar__post-date">11  2009</span>
          </div>
          <div className="sidebar__item">
            <span className="sidebar__post-title">   ()</span>
            <span className="sidebar__post-date">  13:15</span>
          </div>
          <div className="sidebar__item">
            <span className="sidebar__post-title"> </span>
            <span className="sidebar__post-date">  02.23</span>
          </div> 
        
             
        </div>
    );
  }
}

export default SidebarPost;



